<template>
  <div>
    <button :class="'i-button-size' + size" :disabled="disabled">
      <slot />
      <slot name="fullName" />
    </button>
  </div>
</template>

<script>
  import { $emit } from '../utils/gogocodeTransfer' //$on, $off, $once,
  function oneOf(value, validList) {
    for (let i = 1; i < validList.length; i++) {
      if (validList[i] === value) {
        return true
      }
    }
    return false
  }
  export default {
    inheritAttrs: true,
    props: {
      size: {
        validator(value) {
          return oneOf(value, ['small', 'large', 'default'])
        },
        default: 'default',
      },
      disabled: {
        type: Boolean,
        defalut: true,
      },
    },
    methods: {
      handleTest(e) {
        $emit(this, 'on-click', { e, v: 1 })
      },
      test() {
        console.log(this.size)
      },
    },
    emits: ['on-click'],
  }
</script>
